   /* src/index.css */
   body {
    margin: 0;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    background-color: #f3f0ff;
    color: #4a148c;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
  }

  .app-container {
    width: 100%;
    max-width: 800px;
    padding: 20px;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
    background-color: #fff;
    border-radius: 10px;
  }

  .app-header {
    background-color: #6a1b9a;
    color: white;
    padding: 20px;
    text-align: center;
    border-radius: 10px 10px 0 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .app-header h1 {
    margin: 0;
  }

  .connect-button {
    background-color: #8e24aa;
    color: white;
    border: none;
    padding: 10px 20px;
    margin: 10px 0;
    cursor: pointer;
    border-radius: 5px;
    transition: background-color 0.3s ease;
    font-weight: bold;
  }

  .connect-button:hover {
    background-color: #7b1fa2;
  }
  
  .welcome-message {
    text-align: center;
    padding: 40px 20px;
    background-color: #f3f0ff;
    border-radius: 10px;
    margin: 20px 0;
  }
  
  .welcome-message h2 {
    color: #6a1b9a;
    margin-bottom: 20px;
  }
  
  .welcome-message p {
    font-size: 1.2em;
    color: #4a148c;
  }

  .user-guide {
    margin: 20px 0;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 8px;
  }

  .loan-form {
    max-width: 400px;
    margin: 20px auto;
    padding: 20px;
    background-color: white;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }

  input {
    width: calc(100% - 22px);
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #ddd;
    border-radius: 5px;
  }

  input:focus {
    border-color: #8e24aa;
    outline: none;
  }

  .submit-button {
    background-color: #8e24aa;
    color: white;
    border: none;
    padding: 10px 20px;
    margin: 10px 0;
    cursor: pointer;
    border-radius: 5px;
    transition: background-color 0.3s ease;
  }
  
  .submit-button:disabled {
    background-color: #cccccc;
    cursor: not-allowed;
  }

  .submit-button:hover:not(:disabled) {
    background-color: #7b1fa2;
  }

  .loan-details, .transaction-details {
    margin: 20px 0;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 8px;
  }
  
  .transaction-details h2 {
    color: #6a1b9a;
    margin-bottom: 15px;
  }
  
  .transaction-grid {
    display: grid;
    gap: 10px;
  }
  
  .transaction-item {
    display: flex;
    padding: 8px;
    background-color: white;
    border-radius: 5px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.05);
  }
  
  .transaction-item .label {
    font-weight: bold;
    min-width: 150px;
    color: #4a148c;
  }
  
  .transaction-item .value {
    word-break: break-all;
    font-family: monospace;
    color: #333;
  }
  
  .explorer-link {
    margin-top: 15px;
    text-align: center;
  }
  
  .explorer-link a {
    display: inline-block;
    padding: 8px 15px;
    background-color: #8e24aa;
    color: white;
    text-decoration: none;
    border-radius: 5px;
    font-weight: bold;
    transition: background-color 0.3s ease;
  }
  
  .explorer-link a:hover {
    background-color: #7b1fa2;
  }

  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
  }

  /* Tabs */
  .tabs {
    display: flex;
    margin: 20px 0;
    border-bottom: 1px solid #ddd;
  }

  .tab-button {
    padding: 10px 20px;
    background-color: transparent;
    border: none;
    cursor: pointer;
    font-weight: bold;
    color: #8e24aa;
    border-bottom: 3px solid transparent;
    transition: all 0.3s ease;
  }

  .tab-button:hover {
    background-color: #f3f0ff;
  }

  .tab-button.active {
    border-bottom: 3px solid #8e24aa;
    color: #6a1b9a;
  }

  /* Loan List */
  .loan-list {
    margin: 20px 0;
  }

  .loans-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 20px;
    margin-top: 20px;
  }

  .loan-card {
    background-color: white;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    padding: 15px;
    transition: transform 0.3s ease;
  }

  .loan-card:hover {
    transform: translateY(-5px);
  }

  .loan-card h3 {
    color: #6a1b9a;
    margin-top: 0;
    border-bottom: 1px solid #f1f1f1;
    padding-bottom: 10px;
  }

  .loan-card.repaid {
    opacity: 0.7;
  }

  .loan-details {
    margin-bottom: 15px;
  }

  .loan-details p {
    margin: 8px 0;
    display: flex;
    justify-content: space-between;
  }

  .loan-details .label {
    font-weight: bold;
    color: #4a148c;
  }

  .loan-details .value {
    text-align: right;
  }

  .loan-actions {
    display: flex;
    justify-content: center;
    gap: 10px;
    margin-top: 15px;
  }

  .fund-button, .repay-button {
    padding: 8px 16px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-weight: bold;
    transition: background-color 0.3s ease;
  }

  .fund-button {
    background-color: #4caf50;
    color: white;
  }

  .fund-button:hover:not(:disabled) {
    background-color: #388e3c;
  }

  .repay-button {
    background-color: #2196f3;
    color: white;
  }

  .repay-button:hover:not(:disabled) {
    background-color: #1976d2;
  }

  .fund-button:disabled, .repay-button:disabled {
    background-color: #cccccc;
    cursor: not-allowed;
  }

  .error-message {
    background-color: #ffebee;
    color: #c62828;
    padding: 15px;
    border-radius: 5px;
    margin: 15px 0;
    border-left: 4px solid #c62828;
    font-weight: bold;
  }

  .loading {
    text-align: center;
    padding: 30px;
    color: #8e24aa;
    font-style: italic;
  }

  .loans-section {
    padding: 10px;
  }

  /* Add styles for the loan instructions and dismiss button */

  .loan-instructions {
    background-color: #f3f0ff;
    padding: 15px 20px;
    border-radius: 8px;
    margin-bottom: 20px;
  }

  .loan-instructions h2 {
    color: #6a1b9a;
    margin-top: 0;
  }

  .loan-instructions ul {
    padding-left: 20px;
  }

  .loan-instructions li {
    margin-bottom: 8px;
  }

  .explorer-link {
    display: flex;
    justify-content: center;
    gap: 15px;
    margin-top: 15px;
  }

  .clear-tx-button {
    padding: 8px 15px;
    background-color: #f5f5f5;
    color: #555;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-weight: bold;
    transition: background-color 0.3s ease;
  }

  .clear-tx-button:hover {
    background-color: #e0e0e0;
  }

  /* Status indicators */
  .status-pending {
    color: #ff9800;
    font-weight: bold;
  }

  .status-active {
    color: #4caf50;
    font-weight: bold;
  }

  .status-repaid {
    color: #9e9e9e;
    font-weight: bold;
  }

  .owner-indicator {
    background-color: #e8eaf6;
    padding: 4px 8px;
    border-radius: 4px;
    font-size: 0.85em;
    color: #3f51b5;
    display: inline-block;
    margin-top: 8px;
  }